<template>
  <el-upload
    class="avatar-uploader"
    :action="baseURL + '/head'"
    name="header"
    :show-file-list="false"
    :with-credentials="true"
    :on-success="handleAvatarSuccess"
    :before-upload="beforeAvatarUpload"
  >
    <!-- 缩略图 -->
    <img v-if="imageUrl" :src="imageUrl" class="avatar" />
    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
  </el-upload>

  <el-button type="success" size="mini" @click="upload">上传</el-button>
</template>

<script>
export default {
  name: "UserHeadimg",
  data() {
    return {
      imageUrl: "",
    };
  },
  methods: {
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    handleAvatarSuccess(res, file) {
      if (res.code) return this.$message.error(res.message); //上传失败
      this.imageUrl = URL.createObjectURL(file.raw); //设置缩略图的路径
    },
    upload() {
      console.log(1);
      if (!this.imageUrl) return this.$message.error("请选择图片");
      this.$axios({
        method: "POST",
        url: "/headimg",
      }).then((res) => {
        this.updateuserImg(res.data);
        this.imageUrl = "";
      });
    },
  },
};
</script>

<style lang="less" scoped>
/deep/.el-upload {
  position: relative;
  overflow: hidden;
  border: 1px dashed #d9d9d9;
  cursor: pointer;
  border-radius: 6px;
  .avatar {
    display: block;
    max-width: 178px;
    max-height: 178px;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
}
</style>