<template>
  <div class="dialog" v-if="show">
    <div class="content">
      <el-tabs v-model="activeName">
        <el-tab-pane label="登录" name="first">
          <el-form
            :model="loginForm"
            :rules="rules"
            ref="loginForm"
            label-width="100px"
          >
            <el-form-item label="用户名" prop="user">
              <el-input v-model="loginForm.user"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input type="password" v-model="loginForm.password"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="loginSubmit">登录</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="注册" name="second">
          <el-form
            :model="regForm"
            :rules="rules"
            ref="regForm"
            label-width="100px"
          >
            <el-form-item label="用户名" prop="user">
              <el-input v-model="regForm.user"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input type="password" v-model="regForm.password"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="password2">
              <el-input type="password" v-model="regForm.password2"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="regSubmit">注册</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import { mapMutations,mapState} from "vuex";
export default {
  name: "Login",
  data() {
    return {
      show:true,
      activeName: "first",
      regForm: {
        //注册表单内控件绑定的数据
        user: "",
        password: "",
        password2: "",
      },
      loginForm:{ //登录表单内控件绑定的数据
        user: "",
        password: "",
      },
      rules: {
        //form表单里rules属性绑定的对象，用来对表单内控件做格式校验
        user: [
          { required: true, message: "请输入用户名" },
          {
            validator(rule, value, callback) {
              //长度
              if (value.length < 2 || value.length > 8) {
                callback(new Error("请输入2-8位字符"));
              } else {
                //字符验证
                if (/^[a-zA-Z0-9_\u4e00-\u9fa5]+$/.test(value)) {
                  callback(); //验证成功
                } else {
                  callback(new Error("只允许汉字、字母、下划线"));
                }
              }
            },
          },
        ],
        password: [
          { required: true, message: "请输入密码" },
          {
            validator(rule, value, callback) {
              //长度
              if (value.length < 6 || value.length > 18) {
                callback(new Error("请输入6-18位字符"));
              } else {
                //字符验证
                if (/^[a-zA-Z]\w{5,17}$/.test(value)) {
                  callback(); //验证成功
                } else {
                  callback(new Error("以字母开头,只能包含字符、数字和下划线"));
                }
              }
            },
          },
        ],
        password2: [
          { required: true, message: "请确认密码" },
          {
            validator: (rule, value, callback) => {
              // console.log(this);
              if (this.regForm.password === value) {
                callback(); //密码一致
              } else {
                callback(new Error("两次密码不一致"));
              }
            },
          },
        ],
      },
    };
  },
  computed:{
    ...mapState(['userInfo'])
  },
  methods: {
    hide(){
      if(this.userInfo.name){
        this.show = false
      }else{
        this.show = true
      }
    },
    regSubmit() {
      //注册的提交按钮  发起请求到后端
      this.$refs.regForm.validate(async (bool) => {
        if (bool) {
          //表单数据校验成功
          let { data } = await this.$axios({
            method: "POST",
            url: "/sing",
            data: {
              user: this.regForm.user,
              password: this.regForm.password,
            },
          });
          if (data.cors) {
            //注册失败
            this.$message.error(data.message);
          } else {
            //注册成功
            this.$message.success("注册成功");
            this.activeName = "first";
            this.$refs.regForm.resetFields(); //清空表单内的数据
          }
        } else {
          return false;
        }
      });
    },
    loginSubmit(){
      //登录的提交按钮  发起请求到后端 
      this.$refs.loginForm.validate(async (bool) => {
        if(bool){//表单数据校验成功
          let { data } = await this.$axios({
            method: "POST",
            url: "/long",
            data: this.loginForm
          });
          if(data.cors){
            this.$message.error(data.massage);
          }else{
            // console.log(data);
            this.$message.success("登录成功");
            this.$refs.regForm.resetFields(); //清空表单内的数据
            this.show = false;//因此弹窗
            //将响应过来的用户数据存到vuex
            this.updateUserInfo(data.data[0]);
          }

        }else{
          return false;
        }
      });
    },
    //使用vuex里的updateUserInfo()
    ...mapMutations(["updateUserInfo"])
  },
  created(){
    this.hide()
  } 
};
</script>

<style lang="less" scoped>
.el-button {
  margin: 11px 0;
}
/deep/ .el-tabs__item{
    color: black;
    font-weight: bold;
}
/deep/ .el-form-item__label{
    font-weight: bold;
    color: black;
}
.dialog {
  .content {
    margin: 100px auto;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 30px;
    width: 400px;
    padding: 10px 20px;
  }
}
</style>