<template>
  <div class="about">
    <article>
      <h2>请输入你的回复</h2>
      <el-input
        v-model="textarea"
        :rows="1"
        placeholder="请输入内容"
        type="textarea"
      />
      <div class="publish">
        <div class="emojix-box">
          <i class="iconfont icon-biaoqing" @click="showEmoji"></i>
          <div class="emoji" v-show="isShow">
            <ul>
              <li
                v-for="(item, index) in arrEmoji"
                :key="index"
                @click="printEmoji(item)"
              >
                {{ item }}
              </li>
            </ul>
          </div>
        </div>
        <el-button type="primary" size="mini" @click="submit"
          >发表留言</el-button
        >
      </div>
    </article>
    <article class="comment-area">
      <ul>
        <li v-for="item in arrMessage" :key="item.id">
          <div class="comment-wrap">
            <img :src="`${baseURL}${item.author.img}`" />
            <!--用户头像-->
            <div class="message-box">
              <!-- 右侧的盒子 -->
              <div class="name-time">
                <span>{{ item.author.user }}</span>
                <span>{{ formatDate(item.data) }}</span>
              </div>
              <p class="text">{{ item.content }}</p>
              <div class="like-reply">
                <i
                  class="iconfont icon-xinaixin"
                  :class="{
                    red: userInfo._id && item.likes.includes(userInfo._id),
                  }"
                  @click="commentLikes(item._id)"
                >
                </i>
                <span>{{ item.likes.length }}</span>
                <i class="iconfont icon-huifu" @click="commentReply(item)"></i>
              </div>
            </div>
          </div>
          <!-- 回复(子级/二级) -->
          <div
            class="reply-wrap"
            v-for="(childItem, index) in item.children"
            :key="childItem._id"
          >
            <img :src="`${baseURL}${childItem.author.img}`" />
            <div class="message-box">
              <div class="name-time">
                <span>{{ childItem.author.name }}</span>
                <span>{{ formatDate(childItem.data) }}</span>
              </div>
              <p class="text">
                <span>@{{ childItem.replyUser.name }}</span
                >{{ childItem.content }}
              </p>
              <div class="like-reply">
                <i
                  class="iconfont icon-xinaixin"
                  @click="replayLikes(item._id, childItem._id, index)"
                  :class="{
                    red: userInfo._id && childItem.likes.includes(userInfo._id),
                  }"
                >
                </i>
                <span>{{ childItem.likes.length }}</span>
                <i
                  class="iconfont icon-huifu"
                  @click="childReply(item, childItem)"
                ></i>
              </div>
            </div>
          </div>
          <!-- 回复框 -->
          <div class="print-reply" :class="{ show: item.isShowReply }">
            <el-input
              size="mini"
              v-model="item.replyContent"
              :placeholder="`@${item.replyUser.name}`"
            ></el-input>
            <el-button type="primary" size="mini" @click="submitReply(item)"
              >提交回复</el-button
            >
          </div>
        </li>
      </ul>
    </article>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "Message",
  data() {
    return {
      textarea: "",
      isShow: false,
      arrEmoji: [
        "😃",
        "😁",
        "😂",
        "😊",
        "🙃",
        "🥰",
        "😘",
        "🤭",
        "🤔",
        "😏",
        "😵",
        "😟",
        "🙁",
        "😳",
        "😰",
        "😭",
        "😖",
        "😡",
        "😠",
        "😈",
        "🤡",
        "☠️",
        "👻",
        "🙈",
        "🙉",
        "❤️",
        "💖",
        "💢",
        "💣",
        "💤",
        "👋",
        "👍",
        "👌",
        "🤞",
        "🤟",
        "🤙",
        "👊",
        "🖕",
        "🤝",
        "🙏",
        "🙅",
        "🙅‍♂️",
        "👨‍✈️",
        "👩‍✈️",
        "👷",
        "👷‍♀️",
        "🤦",
        "🤦‍♂️",
        "👴",
        "🧓",
        "🐴",
        "🐮",
        "🐖",
        "🐑",
        "🐓",
        "🦚",
        "🐲",
        "🐳",
        "🐟",
        "🐌",
      ],
      arrMessage: [],
    };
  },
  computed: {
    ...mapState(["userInfo"]),
  },
  methods: {
    formatDate(date) {
      let d = new Date(parseInt(date));
      let YY = d.getFullYear(),
        MM = d.getMonth() + 1,
        DD = d.getDate(),
        hh = d.getHours(),
        mm = d.getMinutes(),
        ss = d.getSeconds();
      return `${YY}-${MM}-${DD} ${hh}:${mm}:${ss}`;
    },
    //给父级评论点赞
    async commentLikes(commentId) {
      //commentId评论的id值
      //判断用户是否登录
      if (!this.userInfo.name)
        return this.$message.error("请登录，登录后才能发表留言哦！");
      /* 
        点击按钮： 点赞 / 取消点赞    后端
      */
      //提交当前的评论id + 当前点击了按钮的用户id
      let { data } = await this.$axios({
        method: "POST",
        url: "/likes/comment",
        data: { id: commentId }, //传第这条评论的id给后端
      });
      if (data.code) return this.$message.error(data.message); //点赞失败
      //点赞成功
      this.getMessage(); //重新请求获取全部的留言数据
    },

    async commentReply(item) {
      //item父级评论数据对象
      //判断用户是否登录
      if (!this.userInfo.name)
        return this.$message.error("请登录，登录后才能发表留言哦！");
      // item.isShowReply = !item.isShowReply; //显示/隐藏 回复框

      //判断一级评论的作者 是否 不等于 存储里的replyUser里的用户id， 点开
      if (item.author._id !== item.replyUser._id) {
        item.isShowReply = true;
      } else {
        item.isShowReply = !item.isShowReply; //点击显示/隐藏
      }
      //修改@后面的名字。  修改一级评论对象里的replayUser = 父评论的作者数据
      item.replyUser = item.author;
    },

    //发表留言
    submit() {
      if (!this.userInfo.name) {
        return this.$message.error("请登录，登录后才能发表留言哦！");
      }
      let valMsg = this.textarea.trim();
      this.$axios({
        method: "POST",
        url: "/publish",
        data: { valMsg },
      }).then((data) => {
        console.log(data);
        if (data.data.code) return this.$message.error(data.data.message); //提交失败
        this.$message.success(data.data.message);
        this.getMessage(); //重新请求获取全部的留言数据
        this.textarea = ""; //清空文本域内容
      });
    },

    async getMessage() {
      let { data } = await this.$axios({
        method: "GET",
        url: "/get/msg",
      });
      data.data.forEach((item) => {
        //这一层的遍历都是对 一级评论进行遍历
        item.isShowReply = false; //控制当前这条评论下的回复框显示 / 隐藏
        item.replyContent = ""; //控制当前评论下的回复input框里的内容
        item.replyUser = item.author; //子级的父级作者id
      });
      this.arrMessage = data.data; //将数据赋值给数组
    },

    //点击父级评论的回复按钮：显示 / 隐藏   回复框
    async commentReply(item) {
      //item父级评论数据对象
      //判断用户是否登录
      if (!this.userInfo.name)
        return this.$message.error("请登录，登录后才能发表留言哦！");
      // item.isShowReply = !item.isShowReply; //显示/隐藏 回复框

      //判断一级评论的作者 是否 不等于 存储里的replyUser里的用户id， 点开
      if (item.author._id !== item.replyUser._id) {
        item.isShowReply = true;
      } else {
        item.isShowReply = !item.isShowReply; //点击显示/隐藏
      }
      //修改@后面的名字。  修改一级评论对象里的replayUser = 父评论的作者数据
      item.replyUser = item.author;
    },

    //点击子级回复的  回复按钮，显示/隐藏回复框
    childReply(item, childItem) {
      //判断用户是否登录
      if (!this.userInfo.name)
        return this.$message.error("请登录，登录后才能发表留言哦！");
      //判断，当前点击了按钮的作者 是否 与  所在的一级评论的作者不相同
      if (childItem.author._id !== item.replyUser._id) {
        item.isShowReply = true;
      } else {
        item.isShowReply = !item.isShowReply; //点击显示/隐藏
      }
      //点击时，设置一级评论对象里的replyUser =  子评论的作者（我要@的用户）
      item.replyUser = childItem.author;
    },

    //点击子级回复的 点赞按钮
    async replayLikes(parentId, childId, childIndex) {
      //一级评论的评论id /子级回复的评论id / 子级回复在children中的下标
      //判断用户是否登录
      if (!this.userInfo.name)
        return this.$message.error("请登录，登录后才能发表留言哦！");
      //提交数据
      let { data } = await this.$axios({
        method: "POST",
        url: "/like/replay",
        data: { parentId, childId, childIndex },
      });
      if (data.code) return this.$message.error(data.message); //回复失败
      this.getMessage(); //重新获取最新的留言数据
    },

    //点击父级评论下的回复提交按钮，提交子回复数据到后端
    async submitReply(item){
      //判断用户是否登录
      if(!this.userInfo.name) return this.$message.error("请登录，登录后才能发表留言哦！");
      let content = item.replyContent.trim();
      if(!content) return this.$message.error("请输入回复的内容！");
      if(content.length > 100) return this.$message.error("字数不能超过100");

      let {data} = await this.$axios({
        method:"POST",
        url:"/reply/submit",
        data:{
          id: item._id,  //被评论的评论id
          content, //回复的内容
          replyUser: item.replyUser._id  //父级评论的作者id， a回复的b，这是b的id
        }
      });
      if(data.code) return this.$message.error(data.message); //回复失败
      this.$message.success(data.message); //回复成功
      this.getMessage();//重新获取所有的留言数据
    },

    showEmoji() {
      this.isShow = !this.isShow; //显示/关闭表情框
    },

    printEmoji(item) {
      if (this.textarea.length >= 99) return;
      this.textarea += item; //点击输入表情到文本域
    },
  },
  created() {
    this.getMessage();
  },
};
</script>

<style lang="less" scoped>
article {
  width: 100%;
  margin-bottom: 15px;
  padding: 25px 20px;
  background-color: rgba(255, 255, 255, 0.3);
  font-family: Quicksand, Microsoft YaHei, sans-serif;
  box-sizing: border-box;
  // box-shadow: 0 0 4px #ddd;
  h2 {
    margin: 0 auto;
    margin-bottom: 15px;
    font-size: 20px;
    width: 60%;
    letter-spacing: 3px;
    line-height: 24px;
    font-family: 隶书;
    // border-left: 5px solid #73b899;
    text-indent: 10px;
  }
  p {
    text-indent: 2em;
    color: #444;
    font-size: 14px;
    letter-spacing: 4px;
    line-height: 30px;
  }
  /deep/ textarea {
    width: 60%;
    margin: 10px auto;
  }
  .publish {
    display: flex;
    // background: #fff;
    width: 60%;
    justify-content: space-between;
    align-items: center;
    // margin-top: 10px;
    margin: 0 auto;
    .emojix-box {
      position: relative;
      .emoji {
        position: absolute;
        background: #fff;
        top: 25px;
        ul {
          display: flex;
          flex-wrap: wrap;
          width: 250px;
          border-left: 1px solid #d8d6d6;
          border-bottom: 1px solid #d8d6d6;
          li {
            overflow: hidden;
            width: 25px;
            height: 25px;
            border-right: 1px solid #d8d6d6;
            border-top: 1px solid #d8d6d6;
            line-height: 25px;
            font-size: 18px;
            box-sizing: border-box;
          }
        }
      }
    }
  }
}
article.comment-area {
  ul li {
    padding-bottom: 5px;
    margin-bottom: 10px;
    border-bottom: 1px dashed #ddd;
    font-size: 12px;
    font-family: Microsoft YaHei, "sans-serif";
    .reply-wrap {
      margin: 10px 8px 8px 55px;
    }
    .comment-wrap,
    .reply-wrap {
      //评论
      display: flex;
      img {
        width: 40px;
        height: 40px;
        margin-right: 15px;
        border-radius: 50%;
      }
      .message-box {
        .name-time {
          font-size: 12px;
          margin-bottom: 3px;
          span:nth-child(1) {
            color: #73b899;
          }
          span:nth-child(2) {
            color: #fff;
            // margin-left: 10px;
          }
        }
        p.text {
          text-indent: 0;
          letter-spacing: 2px;
          color: black;
          margin-bottom: 3px;
          word-break: break-word;
          span {
            color: blue;
          }
        }
        .like-reply {
          i {
            cursor: pointer;
            font-size: 14px;
            color: #fff;
          }
          .red {
            color: red;
          }
          span {
            margin-right: 40px;
            font-size: 12px;
            color: #fff;
          }
        }
      }
    }
    // 回复框
    .print-reply {
      overflow: hidden;
      width: 100%;
      height: 0;
      padding-left: 55px;
      box-sizing: border-box;
      transition: height 0.2s;
      &.show {
        height: 60px;
      }
      .el-button {
        float: right;
      }
    }
  }
}
</style>