<template>
    <div>
    <article>
        <el-tabs v-model="activeName">
            <el-tab-pane label="发表文章" name="first">
                <AddArticle></AddArticle>
            </el-tab-pane>
            <el-tab-pane label="文章管理" name="second">
                <ArticleManager></ArticleManager>
            </el-tab-pane>
            <el-tab-pane label="相片上传" name="third">
                <Phone></Phone>
            </el-tab-pane>
            <el-tab-pane label="相册管理" name="fourth">
                <Phoneall></Phoneall>
            </el-tab-pane>
            <el-tab-pane label="反馈收集" name="fifth">
                <ContactManager></ContactManager>
            </el-tab-pane>
        </el-tabs>
    </article>
  </div>
</template>

<script>
import Phoneall from '@/components/admin/Phoneall'
import Phone from '@/components/admin/Phone'
import AddArticle from '@/components/admin/AddArticle'
import ArticleManager from '@/components/admin/ArticleManager'
import ContactManager from '@/components/admin/ContactManager'
export default {
  name:"Admin",
    data(){
        return{
            activeName:"first"
        }
    },
    components:{
      Phoneall,
      Phone,
      AddArticle,
      ArticleManager,
      ContactManager
    },
      methods:{
    // 验证是否具有管理员权限
    async isCheck(){
      let {data} = await this.$axios({
          method: "POST",
          url:"/administer/isCheck"
      });
    //   console.log(data);
      if(data.code){ //不是管理员
        this.$router.replace("/"); //重定向首页路由
        this.$message.error(data.message)
      }else{
        this.$message.success(data.message);
      }
    }
  },
  created(){
    this.isCheck();   
  }
}
</script>

<style lang="less" scoped>
article{
  /deep/ .el-tabs__header{
    width: 40%;
  }
  /deep/ .el-tabs__item{
    color: #fff;
    font-weight: bold;
  }
}
</style>