<template>
  <div class="album">
    <ul>
      <li v-for="(item,index) in phoneAll" :key="item._id">
        <div class="demo-image__preview">
          <el-image
            style="width: 100px; height: 100px"
            :src="`${baseURL}${item.imgUrl}`"
            :preview-src-list="phoneurl"
            :initial-index="0"
            fit="cover"
          >
          </el-image>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Album",
  data() {
    return {
      phoneAll: [],
      phoneurl: [],
    };
  },
  methods: {
    async getphone() {
      let { data } = await this.$axios({
        method: "GET",
        url: "/get/phone",
      });
      if (data.code) return this.$message.error(data.message); //没请求到数据就提示
      console.log(data.data);
      this.phoneAll = data.data
      this.phoneAll.forEach(v=>{
        this.phoneurl.push(this.baseURL + v.imgUrl)
      })
    },
  },
  created() {
    this.getphone();
  },
};
</script>


<style lang="less" scoped>
.album{
    ul{
        display: flex;
        flex-wrap: wrap;
        // justify-content: space-between;
        li{
          width: 150px;
          height: 150px;
          margin-left: 10px;
          margin-top: 10px;
          // margin: 10px auto;
        }
    }
}
.demo-image__error .image-slot {
  font-size: 30px;
}
.demo-image__error .image-slot .el-icon {
  font-size: 30px;
}
.demo-image__error .el-image {
  width: 100%;
  height: 200px;
}
</style>